// src/pages/Skills.js
import React from "react";
import { motion } from "framer-motion";
import { Container, Typography, Grid, Box } from "@mui/material";


// src/data/skillsData.js
const skillsData = [
  {
    title: "Développement Web",
    skills: [
      { name: "HTML & CSS", description: "Création de pages web avec HTML5 et CSS3, responsive design." },
      { name: "JavaScript & TypeScript", description: "Développement d'applications dynamiques et interactives." },
      { name: "React & Redux", description: "Développement de composants React et gestion de l'état global avec Redux." },
    ],
  },
  {
    title: "Développement Logiciel",
    skills: [
      { name: "Python", description: "Développement de scripts et d'applications backend." },
      { name: "Java", description: "Création d'applications robustes et maintenables." },
    ],
  },
  {
    title: "Cybersécurité",
    skills: [
      { name: "Sécurité Réseau", description: "Protection et sécurisation des réseaux d'entreprise." },
      { name: "Audit de Sécurité", description: "Évaluation de la sécurité des systèmes et des applications." },
    ],
  },
  {
    title: "Cloud et Virtualisation",
    skills: [
      { name: "AWS & Azure", description: "Déploiement et gestion d'infrastructures cloud." },
      { name: "Docker & Kubernetes", description: "Orchestration de conteneurs pour une meilleure gestion des applications." },
    ],
  },
];



const Skills = () => {
  return (
    <Container>
      <Typography class="Heading ">Mes Compétences</Typography>
      <Typography class="Subheading" > 
        Voici un aperçu des technologies et compétences que je maîtrise dans les
        domaines du développement web, du développement logiciel, de la
        cybersécurité, et du cloud et de la virtualisation.
      </Typography>
      {skillsData.map((category, index) => (
        <Box key={index}>
          <Typography class="Subheading">{category.title}</Typography>
          <Grid>
            {category.skills.map((skill, idx) => (
              <Grid>
                <Box>
                  <Typography class="Body1">{skill.name}</Typography>
                  <Typography class="Body2">{skill.description}</Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>
      ))}
    </Container>
  );
};

export default Skills;
